.about .Skills {
    transition: .3s;
    padding-top: 40px;
    padding-bottom: 80px;
}

.about .Skills .progress {
    transition: .3s;
    margin-bottom: 20px;
    margin-top: 20px;
    width: 100%;
    display: block;
}

.about .Skills .progress-bar {
    transition: .3s;
    display: block;
    background-color: rgba(150, 150, 150, 0.5);
    border-radius: 10px;
    text-align: center;
}

.about .Skills .progress-bar span {
    transition: .3s;
    color: var(--DarkPrimary);
    background-color: var(--DarkPrimary);
    display: block;
    padding: 5px;
    border-radius: 5px;
    cursor: help;
    font-size: 12px;
}

.theme-light .about .Skills .progress:hover .progress-bar span {
    transition: .3s;
    color: var(--White);
}

.theme-dark .about .Skills .progress:hover .progress-bar span {
    transition: .3s;
    color: #000;
}

@media (max-width: 725px) {
    .about h1 {
        transition: .3s;
        font-size: 24px;
    }

    .about p {
        transition: .3s;
        font-size: 15.5px;
    }

    .about .flex * {
        transition: .3s;
        font-size: 14px;
    }
}

@media (max-width: 500px) {
    .about .hero {
        transition: .3s;
        align-items: start;
        text-align: start;
        justify-content: start;
    }

    .about .About-title {
        transition: .3s;
        font-size: 12px;
        position: relative;
        top: 4px;
    }

    .about p {
        transition: .3s;
        text-align: start;
        width: 100%;
        max-width: 100%;
    }

    .about .flex {
        transition: .3s;
        flex-direction: column;
        width: 100%;
        gap: .5rem;
    }

    .about .flex a:last-child {
        transition: .3s;
        padding: 0 16px;
    }
}